@font-face {
  font-family: 'Gilroy';
  src: url('Gilroy-Light.eot');
  src: local('Gilroy Light'), local('Gilroy-Light'), url('Gilroy-Light.eot?#iefix') format('embedded-opentype'), url('Gilroy-Light.woff') format('woff'), url('Gilroy-Light.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: 'Gilroy';
  src: url('Gilroy-Extrabold.eot');
  src: local('Gilroy Extrabold'), local('Gilroy-Extrabold'), url('Gilroy-Extrabold.eot?#iefix') format('embedded-opentype'), url('Gilroy-Extrabold.woff') format('woff'), url('Gilroy-Extrabold.ttf') format('truetype');
  font-weight: 800;
  font-style: normal;
}
body {
  margin: 0px;
  padding: 0px;
  font-family: 'Gilroy', "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #222222;
  background-color: #FFFFFF;
}
.page {
  height: 100%;
}
.page-body {
  display: grid;
  grid-auto-rows: min-content 1fr min-content;
  min-height: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 1100px;
  margin: 0 auto;
  padding: 0;
}
@media (max-width: 768px) {
  .container {
    width: 768px;
  }
}
@media (max-width: 425px) {
  .container {
    width: 300px;
  }
}
a {
  text-decoration: none;
  color: #222222;
}
a:hover {
  font-weight: bold;
  display: inline-block;
}
a:active {
  font-weight: bold;
  color: #909090;
}
a:disabled {
  font-weight: bold;
  color: #EBEBEB;
}
ul {
  list-style: none;
  padding: 0px;
  margin: 0px;
}
p {
  margin: 0px;
}
h1 {
  font-size: 64px;
  line-height: 72px;
  text-align: center;
  margin: 0px 0px 48px;
}
h1 span {
  font-size: 62px;
  color: #FFFFFF;
  text-shadow: -1px -1px 0 #222222, 1px -1px 0 #222222, -1px 1px 0 #222222, 1px 1px 0 #222222;
}
h2 {
  margin: 0px 0px 48px;
  font-size: 32px;
  line-height: 40px;
  position: relative;
}
h2::before {
  content: "";
  position: absolute;
  top: 19.5px;
  left: -80px;
  width: 40px;
  height: 1px;
  background-color: #222222;
}
h2 span {
  font-size: 30px;
  color: #FFFFFF;
  text-shadow: -1px -1px 0 #222222, 1px -1px 0 #222222, -1px 1px 0 #222222, 1px 1px 0 #222222;
}
h3 {
  margin: 0px 0px 8px;
}
.header {
  margin-bottom: 136px;
}
.header__nav {
  height: 85px;
  border-bottom: 1px solid #EBEBEB;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.header__nav--icon {
  display: block;
  padding: 34px 34px 33px;
  color: transparent;
}
.header__nav--icon-location {
  background: no-repeat center / 18px url('../../img/icon/map-marker-big.svg');
  border-right: 1px solid #EBEBEB;
}
.header__nav--icon-location:hover {
  padding-inline: 33px 34px;
  background: no-repeat center / 18px url('../../img/icon/map-marker-big-hover.svg');
}
.header__nav--icon-logo {
  background: no-repeat center / 30px url('../../img/icon/logo.svg');
}
.header__nav--icon-logo:hover {
  background: no-repeat center / 30px url('../../img/icon/logo-hover.svg');
}
.header__nav--icon-tel {
  background: no-repeat center / 18px url('../../img/icon/phone.svg');
  border-left: 1px solid #EBEBEB;
}
.header__nav--icon-tel:hover {
  background: no-repeat center / 18px url('../../img/icon/phone-hover.svg');
}
.header__nav-menu {
  display: flex;
  gap: 50px;
}
.header__nav-menu li {
  position: relative;
}
.header__nav-menu li:hover::before {
  font-size: 12px;
  content: "";
  position: absolute;
  bottom: -32px;
  width: 100%;
  height: 1.5px;
  background-color: #222222;
}
.header__title {
  padding-block: 64px 84px;
}
.header__nav-detail {
  color: #FFFFFF;
  font-weight: bold;
  font-size: 20px;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url('../../img/slider/Image.png');
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 100%;
  padding-block: 152px 23px;
}
.header__nav-detail ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.header__nav-detail ul li {
  font-size: 20px;
  width: 20vw;
  text-align: center;
  padding-block: 31px 29px;
}
.button {
  border: 1px solid #909090;
  padding: 10px 48px;
  display: block;
  margin: 0 auto;
  width: min-content;
  font-weight: bold;
  position: relative;
}
.button:hover {
  display: block;
  border: 1px solid #222222;
}
.button:active {
  border: 1px solid #909090;
  color: #909090;
}
.button:disabled {
  border: 1px solid #EBEBEB;
}
.button::before {
  content: "";
  position: absolute;
  top: 19.5px;
  left: -10px;
  width: 20px;
  height: 1px;
  background-color: #222222;
}
.button:active::before {
  background-color: #909090;
}
.button:disabled::before {
  background-color: #EBEBEB;
}
#portfolio {
  margin-bottom: 136px;
}
.portfolio {
  display: grid;
  grid-template-columns: repeat(3, 360px);
  grid-template-rows: repeat(4, 270px);
  gap: 10px;
  grid-template-areas: "card1 card1 card2" "card3 card4 card2" "card5 card6 card7" "card5 card8 card8";
  margin-bottom: 64px;
}
