.portfolio__title {
  margin-top: 32px;
  margin-left: 32px;
  color: #FFFFFF;
  font-size: 20px;
}
#contact {
  margin-bottom: 24px;
}
.contact__map {
  display: flex;
  gap: 10px;
  margin-bottom: 135px;
}
.contact__map-list {
  flex-basis: 100%;
  border: 1px solid #2F80ED;
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  margin-bottom: 30px;
}
.contact__map-list div {
  width: 340px;
}
.contact__map-social {
  display: flex;
  gap: 10px;
}
.contact__map-social li {
  background-color: #222222;
  border-radius: 2px;
  width: 30px;
  height: 30px;
}
.contact__map-form {
  flex-basis: 370px;
  border: 1px solid #DA2128;
}
.contact__copyright {
  border-top: 1.5px solid #222222;
  padding-top: 24.5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
